import { Box } from "@mui/material";
import { Content, Hero, Slider } from "../components";
import Layout from "../layout/layout";
import { getBlogs } from "../interfaces";
import { GetServerSideProps } from "next";
import { getService } from "../services/get-service";

interface HomePageProps {
    blogs: getBlogs[];
}

const IndexPage = ({ blogs }: HomePageProps) => {
    return (
        <Layout>
            <Hero />
            <Box
                sx={{
                    display: "flex",
                    flexDirection: { xs: "column", md: "row" },
                    padding: "20px",
                    mt: "10px",
                    mb: "10px",
                }}
                gap={2}
            >
                <Slider sliderBlog={blogs} />
            </Box>
        </Layout>
    );
};

export default IndexPage;

export const getServerSideProps: GetServerSideProps<
    HomePageProps
> = async () => {
    try {
        const blogs = await getService.GetPosts();

        const safeBlogs = blogs;

        console.log("‚úÖ Bloglar olindi:", safeBlogs.length);

        return {
            props: {
                blogs: safeBlogs,
            },
        };
    } catch (error) {
        console.error("‚ùå Xato:", error);
        return {
            props: {
                blogs: [],
            },
        };
    }
};
