import { getBlogs } from "./../interfaces/index";
import { request, gql } from "graphql-request";

const graphqlAPI =
    "https://us-west-2.cdn.hygraph.com/content/cmkh1ryoj00c207w88juz2699/master";

export const getService = {
    async GetPosts() {
        console.log("üì° API URL:", graphqlAPI);

        const query = gql`
            query MyQuery {
                blogs {
                    author {
                        avatar {
                            url
                        }
                        slug
                        name
                    }
                    excerpt
                    createdAt
                    image {
                        url
                    }
                    slug
                    title
                }
                categories {
                    label
                    slug
                }
            }
        `;

        try {
            const result = await request<{ blogs: getBlogs[] }>(
                graphqlAPI,
                query,
            );
            console.log("Graphql ulangan");

            return result.blogs;
        } catch (error: any) {
            console.error("‚ùå GraphQL xatosi:", error.message);
            return [];
        }
    },
};
